import axios from 'axios';

// Crez une instance d'axios pour votre configuration
const axiosInstance = axios.create({
  baseURL: 'https://gestion-candidats-back.onrender.com/api', // Remplacez cette URL par l'URL de base de votre backend
});

// Ajoutez un intercepteur pour les requtes sortantes
axiosInstance.interceptors.request.use(
  config => {
    const token = localStorage.getItem('token');
    if (token) {
      // Ajoutez le token  l'en-tte d'autorisation pour chaque requte sortante
      config.headers.Authorization = `${token}`;
    }
    return config;
  },
  error => {
    // Grez les erreurs d'intercepteur si ncessaire
    return Promise.reject(error);
  }
);

export default axiosInstance;
